@import url(//fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  top: 0;
  bottom: 0;
  font-family: -apple-system, 'Montserrat', BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ebc634;
  color: #ebc634;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media all and (min-width: 960px){body{font-size:18px}}@media all and (max-width: 959px) and (min-width: 600px){body{font-size:16px}}@media all and (max-width: 599px) and (min-width: 320px){body{font-size:12px}}body{top:0;bottom:0;margin:0;padding:0;border:0;background:#ebc634;width:100%;height:100%;overflow-x:hidden}.navBox{background:-webkit-gradient(linear, left top, left bottom, from(black), color-stop(80%, rgba(0,0,0,0)));background:linear-gradient(black 0%, rgba(0,0,0,0) 80%);width:100vw;position:fixed;margin:0px;padding-top:8px;overflow:hidden;z-index:99999;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.outLinkPicture{vertical-align:middle;width:20px;height:20px}.section{height:101vh;width:100vw}.home{background-image:url(cacciamani10.github.io/static/media/treesandstars2.edc71dda.jpg);background-repeat:no-repeat;background-position:center;background-size:cover}h1.name{color:#ebc634;font-size:3.5em}h3.title,.header-box{color:#ebc634;font-size:1.3em}div.heading{top:30%;left:20%;position:absolute}.about{background-color:#39545f;padding-top:3%;padding-bottom:3%}.projects{background-color:#38454b;padding-top:3%;padding-bottom:3%}.skills{vertical-align:middle;padding:2em;border:3px solid #ebc634;border-collapse:collapse}.skills>table{margin-left:auto;margin-right:auto}.skills>table,th,td{color:#ebc634;padding:2px;border:3px solid #ebc634;border-collapse:collapse}.skills>th,td{white-space:nowrap;list-style-position:inside;padding:1em 1em 1em 0px}th.header-box{text-align:center}.list-header{padding-left:0;font-size:1.1em;font-style:italic;list-style:none}.ssection-header{color:#ebc634;text-align:left;padding-top:20%;font-size:2.6em}.portrait{margin-top:8%;border-radius:50%;border:4px solid #ebc634;width:auto;max-width:10em;display:block;margin-left:auto;margin-right:auto}.card{width:auto;max-width:612px;max-height:350px}.project-description{color:#ebc634}.buttons{margin:10px 25px 10px 0px}.dots{color:#ebc634;text-align:center;font-size:88px}.left-col{padding-right:10%}.right-col{padding-left:10%}.sticky-nav{color:#ebc634 !important;background:none !important}.navbar-light .navbar-nav .nav-link{color:rgba(235,198,52,0.8) !important}.navbar-light .navbar-brand{color:rgba(235,198,52,0.95) !important}


